$text-color: white
$text-color-light: gray
$pip-color: #382020 // #130a03
$p1-color: red
$p11-color: #BB0011
$p12-color: #430011
$p2-color: black
$p3-color: #01d5a2
$p4-color: #897664
$p5-color: #232822

$font-family_1: roboto thin, roboto, calibri
$font-family_2: "Times New Roman", Times, serif
$font-family_3: Open Sans, sans-serif
$font-family_4: "Courier New", Courier, monospace

@keyframes lds-ring
  0%
    rotate: 0deg

  100%
    rotate: 360deg

@keyframes shake-h-anim
  0%
    translate: 6%

  16%
    translate: -6%

  32%
    translate: 4%

  50%
    translate: -4%

  64%
    translate: 2%

  82%
    translate: -2%

  100%
    translate: 0

@keyframes scale-away
  0%
    scale: 1
    translate: 8%

  16%
    scale: .83
    translate: -8%

  32%
    scale: .67
    translate: 5%

  50%
    scale: .5
    translate: -5%

  64%
    scale: .33
    translate: 2%

  82%
    scale: .16
    translate: -2%

  100%
    scale: 0
    translate: 0

@keyframes color
  to
    opacity: .5

@keyframes movUp
  from
    top: 100%

  to
    top: 0

@keyframes movDown
  from
    top: -100%

  to
    top: 0

@keyframes movLeft
  from
    left: -100%

  to
    left: 0

@keyframes movRight
  from
    left: 100%

  to
    left: 0

