*
  -webkit-user-select: none
  user-select: none
  line-height: 1.1em

html
  overflow: hidden
  height: 100%
  font-size: 3vmin

#root
  overflow: hidden
  height: 100%

  > div
    > .lds-ring
      div
        border-color: $text-color transparent transparent transparent

    > #flytext
      box-shadow: 0 0 0 50vh rgba(0, 0, 0, 0.7)

      ~
        *
          pointer-events: none !important

#app
  overflow: hidden
  height: 100%
  outline: none
  display: flex
  flex-direction: column

body
  margin: 0
  background: linear-gradient(to right, $p12-color, $p11-color 25%, $p4-color 38%, $p4-color 61%, $p11-color 74%, $p12-color)
  overflow: hidden
  position: relative
  height: 100%
  z-index: -1

.menu
  position: absolute
  height: 100%
  display: flex
  justify-content: center
  align-items: center
  text-align: center
  color: $text-color
  top: 0
  left: 0
  width: 100vw
  background: $p4-color
  // z-index: 2

  &::before
    width: 100%
    height: 100%
    display: block
    content: ""
    position: absolute
    background: $p11-color
    clip-path: polygon(0 10em, 10em 0, calc(100% - 10em) 0, 100% 10em, 100% calc(100% - 10em), calc(100% - 10em) 100%, 10em 100%, 0% calc(100% - 10em))

  &::after
    width: 100%
    height: 100%
    display: block
    content: ""
    position: absolute
    background: $p2-color
    clip-path: polygon(0 15em, 15em 0, calc(100% - 15em) 0, 100% 15em, 100% calc(100% - 15em), calc(100% - 15em) 100%, 15em 100%, 0% calc(100% - 15em))

  > .menubox
    z-index: 1

.fade-enter
  opacity: 0
  z-index: 1

  &.fade-enter-active
    opacity: 1
    transition: opacity 300ms linear

.fade-exit
  z-index: 0
  opacity: 1

.game ~ .fade-exit
  z-index: 1

  &.fade-exit-active
    opacity: 0
    transition: opacity 300ms linear



.menubox
  // z-index: 0
  flex: 3
  display: flex
  align-items: center
  flex-direction: column
  text-align: center
  width: 100%
  justify-content: center

  .kbutton
    width: 15rem
    &.space
      margin-top: 2rem

.menubox.across
  flex-direction: row
  width: 75%
  margin: 4rem 0 2rem 0
  &.cselect
    width: auto
    flex: 1 1
    margin: 0
    align-items: center
    gap: 2rem
  &.ssinner
    margin: 0
    align-items: center
    width: 100%
    justify-content: space-around
  &.ssouter
    width: auto

.ssBox.profile
  .menubox
    flex: 2

  flex-direction: row

  .pfp
    width: 100%

.graphicbox
  flex: 1 1
  aspect-ratio: 1/1
  width: 10rem
  position: relative

  &::before
    background-color: #423f36
    border-radius: 10px
    aspect-ratio: 1/1
    content: ""
    position: absolute
    width: 85%
    left: 50%
    top: 50%
    translate: -50% -50%
    z-index: -1

  .die
    &:nth-of-type(2)
      right: 0
      position: absolute
      bottom: 0

.graphicbox.dest
  .die
    left: 50%
    position: absolute

    &:nth-of-type(2)
      left: 50%
      position: absolute

    &::after
      width: unset
      height: unset
      border: none
      padding: 0
      opacity: 1
      z-index: 0
      top: 0
      display: block
      color: $p1-color
      font-size: 700%
      position: absolute
      left: 50%
      translate: -50%
      line-height: 55%
      content: '×'

.graphicbox.match
  .die
    &::after
      width: unset
      height: unset
      border: none
      padding: 0
      opacity: 1
      z-index: 0
      color: $pip-color
      content: "☇"
      font-size: 350%
      left: 0
      position: absolute
      rotate: -45deg
      top: 170%
      translate: 50% 0
    &:last-child
      &::after
        bottom: 170%
        left: auto
        right: 0
        rotate: 135deg
        top: auto
        translate: -50% 0

.footer
  bottom: 0
  left: 0
  width: 100%
  display: flex
  justify-content: flex-start
  // padding: 1.4rem
  position: absolute
  align-items: center
  color: $text-color
  z-index: 1
  pointer-events: none

  .text
    font-size: 1.1rem

.fcontain
  display: flex
  flex-direction: row
  gap: 1rem
  align-items: center
  justify-content: center
  pointer-events: all
  margin: 1.4rem 0 1.4rem 1.4rem

  &.right
