    margin-right: 1.4rem

.symb
  background-size: contain
  height: 1.4rem
  background-repeat: no-repeat
  flex: 1 1 auto
  aspect-ratio: 2.8

.backSymb
  aspect-ratio: 1

.dragSymb
  aspect-ratio: 2

.infoSymb
  background-size: contain
  aspect-ratio: 1
  height: 1.7rem

  .text
    position: absolute
    display: none
    background: $p5-color
    padding: 0.8rem
    right: 1.4rem
    width: auto
    bottom: 4rem
    border-radius: 1rem 1rem 0 1rem
