    &::before
      content: "|"
      right: 1.4rem
      position: relative
      font-size: 1.4rem
      line-height: 100%


.title
  width: 90%
  background-size: contain
  aspect-ratio: 1016/393
  margin-bottom: 1.4rem

.subtitle
