.react-switch-label
  // display: flex
  // align-items: center
  // justify-content: space-between
  cursor: pointer
  background: #888
  border-radius: 2px
  position: relative
  transition: background-color .2s
  aspect-ratio: 2/1
  margin: auto
  height: 2.4rem
  border: .1em solid transparent

  &:hover
    border-color: $text-color

  .react-switch-button
    content: ''
    position: absolute
    top: 4px
    left: 4px
    height: calc(100% - 8px)
