  justify-content: center
  align-items: center
  text-align: center
  gap: 1rem

.timer
  background: #050f05
  font-family: $font-family_4
  padding: 0.2rem
  border-radius: 0.2rem
  font-size: 1.4rem

.ssBox.lock
  &::before
    background: $p1-color
    padding: 0 1em
    left: -1em
    top: 1em

  span
    &::after
      content: "copied to clipboard"
      position: absolute
      bottom: -10%
      translate: 0 100%
      left: 0
      width: 100%
      font-size: .8rem
      opacity: 0.5

.slidePane
  display: flex
  align-items: center
  flex-direction: row
  text-align: center
  position: absolute
  transition: .3s

.slidePaneContainer
  flex: 1 1
  display: block
  width: 15rem
  aspect-ratio: 1/1
  position: relative
  margin: 1rem 0

.containerMask
  height: 100%
  position: absolute
  box-sizing: border-box
  mask-image: linear-gradient(to right, transparent, $p2-color 33%, $p2-color 66%, transparent)
  -webkit-mask-image: linear-gradient(to right, transparent, $p2-color 33%, $p2-color 66%, transparent)

.pfp
  width: 7rem
  aspect-ratio: 1/1
  background-size: contain
  box-sizing: border-box
  border-radius: .3rem
  filter: grayscale(0) brightness(100%)
  transition: .3s

.pfphover
  filter: grayscale(80%) brightness(60%)

.pfp.simp
  width: 15rem
  filter: none
  margin-right: -4rem

.charInfo
  display: grid
  gap: 2rem
  background-color: $p5-color
  border: solid $p1-color
  border-width: 0
  transition: .3s
  padding: 2rem
  min-width: 20rem
  min-height: 10rem
  margin: 1em 0
  position: relative

  .slidePaneContainer
    aspect-ratio: auto
    width: 3rem
    margin: 0 auto
    grid-column: 1 /span 2

  .slidePane
    position: relative
    top: 0
    left: 0
    gap: 0
    padding: 0

  .subtitle
    font-size: 2.4rem
    min-width: 25rem
    position: relative
    margin-left: -22rem

    &:first-of-type
      margin-left: -11rem

  &::before
    display: block
    transition: .2s
    width: 100%
    height: 100%
    content: ""
    position: absolute
    z-index: -1
    background: transparent
    left: 0
    top: 0
    translate: 0

  &:hover
    &::before
      background: $p1-color
      padding: 0 1em
      left: -1em
      top: 1em

.navigable .nav-focused > .charInfo::before
  background: $p1-color
  padding: 0 1em
  left: -1em
  top: 1em

.die
  display: grid
  grid-template-areas: "c . a" "f g e" "b . d"
  aspect-ratio: 1/1
  background-color: #f4ebce
  border: .5rem solid #d7cbb3
  border-radius: 10%
  box-sizing: border-box
  grid-template-columns: repeat(3, minmax(0, 1fr))
  grid-template-rows: repeat(3, minmax(0, 1fr))
  padding: .1rem
  // z-index: 0
  position: relative
  transition: .3s

  &::after
    content: ""
    width: 100%
    height: 100%
    position: absolute
    border: inherit
    border-radius: inherit
    padding: 0.05rem
    top: -.5rem
    left: -.5rem
    opacity: .7
    z-index: -1

  &.num2
    .pip
      &:nth-child(1)
        grid-area: f

      &:nth-child(2)
        grid-area: e

  &.caravan
    background-image: url("./img/cardec.png")
    background-size: cover

  &.match
    &::after
      background-color: #72adcf
      border-color: #6297b6

  &.match-all
    &::after
      background-color: #ecd77a
      border-color: #cdbe61

  &.cheated
    &::after
      background-color: $p3-color
      border-color: $p3-color

  &.shrink-pre, &.shrink-out
    &::after
      background-color: $p1-color
      border-color: $p1-color

  &.shrink-out
    animation: scale-away .4s 1 forwards
    &::after
      animation: color .4s 1 backwards

.pip
  display: block
  align-self: center
  justify-self: center
  border-radius: 50%
  background-color: $pip-color
  box-shadow: inset 0 3px #111
  aspect-ratio: 1/1
  width: 85%

  &:nth-child(1)
    grid-area: a

  &:nth-child(2)
    grid-area: b

  &:nth-child(3)
    grid-area: c

  &:nth-child(4)
    grid-area: d

  &:nth-child(5)
    grid-area: e

  &:nth-child(6)
    grid-area: f

  &:nth-child(odd)
    &:last-child
      grid-area: g

.lds-ring
  display: inline-block
  position: absolute
  right: 2rem
  bottom: 2rem
  width: 6rem
  aspect-ratio: 1
  z-index: 3

  div
    box-sizing: border-box
    display: block
    position: absolute
    width: 80%
    height: 80%
    margin: 10%
    border: .8rem solid $p1-color
    border-radius: 50%
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite
    border-color: $p1-color transparent transparent transparent

    &:nth-child(1)
      animation-delay: -0.45s

    &:nth-child(2)
      animation-delay: -0.3s

    &:nth-child(3)
      animation-delay: -0.15s

#flytext
  display: flex
  align-items: center
  flex-direction: column
  position: absolute
  color: $text-color
  top: 50%
  translate: 0 -50%
  width: 200%
  font-size: 2rem
  font-weight: bold
  background-color: $p2-color
  padding: 1rem 0
  opacity: 0
  transition: .4s
  pointer-events: none
  z-index: 99
  gap: 1rem
  .menubox.across
    margin: 0
    gap: 1rem
    .kbutton
      width: 10rem

#flytext.show
  translate: -25% -50%
  opacity: 1
  pointer-events: auto

.shrink-out
  animation: scale-away .4s 1

.scorebox.shake
  animation: shake-h-anim .3s 1

.scorer.shake
  animation: shake-h-anim .3s 1

.charInfo.shake
  &::before
    animation: shake-h-anim .3s 1

#result
  margin: 0

@media screen and (max-width: 40em)
  .fcontain:not(.mobile)
    display: none !important

  .symb:not(.mobile)
    display: none !important

@media (min-width: 40em)
  html
    font-size: 2vmin

  .game
    height: 100%

    ~ .footer
      position: absolute
      background: none

      > .fcontain
        margin: 1.4rem 0 1.4rem 1.4rem

        > .text
          background: none
          padding: 0
          min-width: auto

  .title
    width: 70%
  .ssBox
    width: 25rem
    padding: 2rem 3rem

  .logo.red
      color: $p1-color

  .graphicbox
    width: 16rem
    &.match
      .die
        &::after
          font-size: 600%
    &.dest
      .die
        &::after
          font-size: 1250%
          line-height: 50%
  .settingsItem
    flex-direction: row
    align-items: center
  .settingInput
    align-self: center
  .menubox
    .settingsList
      gap: 4%
      max-height: 30em
    .across.personal
      flex-direction: row
      max-height: 30em
    input
      // width: 100%
      margin: 0
  #flytext
    font-size: 3rem

  .board
    grid-template-columns: 1fr 2fr
    grid-template-rows: 1fr 1fr !important
    grid-gap: 1rem
    //max-width: 560px

  .tubs
    order: 2
    grid-column: 2 /span 1
    grid-row: 1 /span 2

  .roller
    grid-column: 1 /span 1
    grid-row: 2 /span 1
    order: 1

  .info
    grid-column: 1 /span 1
    order: 1
    animation: movLeft .9s 1 ease-out forwards

  #player
    .info
      grid-row: 2 /span 1
      animation: movLeft .9s 1 ease-out forwards
      top: -3rem

    .roller
      grid-row: 1 /span 1
      order: 4
  .pfp
    width: 12rem

  .rollbox
    width: 15rem

  .tub.hover
    &:hover::before
        scale: 1
        background: $p1-color

    &.tubB:hover::before
          background: $p2-color

    &.tubC:hover::before
          background: $p3-color

  .navigable .nav-focused > .tub.hover
    &::before
      scale: 1
      background: $p1-color

    &.tubB::before
          background: $p2-color

    &.tubC::before
          background: $p3-color

@media (min-width: 70em)
  html
    font-size: 2vmin
  .title
    width: 50%
  .menubox.across.TabBar, .menubox.settingsList, .menubox.across.personal
    max-width: 60em
  .game
    display: grid
    grid-template-rows: repeat(2, minmax(0, 1fr))

  .turnCounter
    margin-left: 2rem
    bottom: auto
    translate: 0 0
    right: auto
    top: 2rem
    animation: movLeft .9s 1 ease-out forwards

  .side
    grid-row: 1 / span 1
    grid-column: 1 / 1
    max-height: none

  #player
    grid-row: 2 / span 1

    .info
      grid-column: 1 /span 1
      grid-row: 1 /span 2
      order: 1
      animation: movLeft .9s 1 ease-out forwards
      top: -7rem

    .roller
      grid-column: 1 /span 1
      grid-row: 3 /span 2
      // left: -100vw
      animation: movLeft .9s 1 ease-out forwards
      top: -4rem

  .board
    max-width: none
    grid-template-columns: minmax(0, 2fr) minmax(0, 3fr) minmax(0, 2fr)
    grid-auto-rows: minmax(35px, auto)
    height: 100%
    box-sizing: border-box
    align-items: center
    justify-content: center

  .tubs
    grid-column: 2 /span 1
    grid-row: 1 /span 4
    order: 1

  .roller
    grid-column: 3 /span 1
    grid-row: 1 /span 2
    order: 2
    animation: movRight .9s 1 ease-out forwards
    top: 4rem

  .info
    grid-column: 3 /span 1
    grid-row: 3 /span 2
    order: 3
    animation: movRight .9s 1 ease-out forwards
    top: 5rem

  .kbutton:hover, .kbutton:focus
    background-color: $p1-color !important
    border-color: maroon
    color: $text-color

  .navigable .kbutton.nav-focused
    background-color: $p1-color !important
    border-color: maroon
    color: $text-color

  .scorebox, .name
    font-size: 2rem
  .rollbox
    width: 25rem